/* stylelint-enable */
@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerRegular.woff2") format("woff2"), url("../fonts/MullerRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Muller";
  src: url("../fonts/MullerMedium.woff2") format("woff2"), url("../fonts/MullerMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.centered {
  margin: 0 auto;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 0 auto;
  padding: 0;
  overflow-x: hidden;
  font-family: "Muller", "Arial", sans-serif;
}
a {
  text-decoration: none;
}
header {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}
.header__top {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  width: 88.29%;
  margin: 0 auto;
  padding-top: 48px;
}
.header__contacts-link {
  padding-top: 3px;
}
.header__contacts-link span {
  font-size: 21px;
  font-weight: 400;
  line-height: 25px;
  color: #000000;
}
.header__contacts-link svg {
  transform: translate(-2px, 7px);
}
.header__contacts-link:hover span,
.header__contacts-link:focus span {
  color: #f9572e;
}
.header__main {
  width: 88.29%;
  margin: 0 auto;
  margin-top: 202px;
  margin-bottom: 50px;
}
.header__main-title {
  max-width: 508px;
  margin: 0 0 26px;
  font-size: 64px;
  font-weight: 500;
  line-height: 64px;
  letter-spacing: -0.5px;
  color: #000000;
  transform: translateX(-5px);
}
.header__main-description {
  max-width: 456px;
  margin: 0 0 44px;
  font-size: 21px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: -0.01px;
  color: #000000;
}
.header__main-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 434px;
}
.header__main-link {
  width: 260px;
  height: 50px;
}
.header__main-info {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.01px;
  color: #666666;
}
.header__video {
  position: absolute;
  top: 20px;
  right: 20px;
}
.header__bottom {
  width: 88.29%;
  margin: 0 auto;
  margin-top: auto;
  padding: 0 0 57px 5px;
}
.header__bottom-anchor {
  display: flex;
  width: 250px;
}
.header__bottom-anchor svg {
  margin-right: 12px;
  fill: none;
  transition: transform 0.4s;
}
.header__bottom-anchor svg .arrow-down {
  transform: translate(10px, 0);
}
.header__bottom-anchor svg .arrow-down-big {
  transform: translate(0, 13px);
}
.header__bottom-anchor span {
  font-size: 16px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: -0.01px;
  color: #f9572e;
}
.header__bottom-anchor:hover svg,
.header__bottom-anchor:focus svg {
  transform: scaleX(0.73);
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  background-color: #f9572e;
  border: none;
  border-radius: 25px;
  box-shadow: 0px 7px 15px rgba(249, 87, 46, 0.6);
  transition: background-color 0.4s, box-shadow 0.4s;
}
.button span {
  font-size: 16px;
  font-weight: 500;
  line-height: 18px;
  color: #ffffff;
}
.button:hover,
.button:focus {
  background-color: #ff7b59;
  box-shadow: 0px 15px 20px rgba(249, 87, 46, 0.7);
}
.video__container {
  position: relative;
}
.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  background-color: transparent;
  border: none;
  transform: translate(-125px, -185px);
  cursor: pointer;
}
.video__button svg {
  display: block;
  margin-bottom: 17px;
  fill: none;
}
.video__button svg .play {
  transform: translate(88px, 64px);
}
.video__button svg .play-big {
  display: none;
  transform: translate(79px, 52px);
}
.video__button span {
  font-size: 21px;
  font-weight: 400;
  line-height: 30px;
  color: #f9572e;
}
.video__button:hover svg .play-big,
.video__button:focus svg .play-big {
  display: block;
}
.video__button:hover svg .play,
.video__button:focus svg .play {
  display: none;
}
.advantages {
  width: 980px;
  margin: 0 auto;
  padding-top: 158px;
}
.advantages__slogan {
  margin: 0 0 7px;
  font-size: 40px;
  font-weight: 500;
  line-height: 50px;
  letter-spacing: -0.1px;
  color: #000000;
}
.advantages__description {
  margin: 0 0 61px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.62px;
  color: #33b5a3;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.advantages__item {
  width: 450px;
  margin-bottom: 57px;
  padding-top: 33px;
  padding-bottom: 37px;
  border-top: 4px solid #33b5a3;
}
.advantages__item .advantages__title {
  margin: 0 0 11px;
  padding-left: 3px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  letter-spacing: -0.05px;
  color: #000000;
}
.advantages__item .advantages__feature {
  margin: 0;
  padding-left: 3px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #666666;
}
.advantages__item--special {
  padding-top: 38px;
  border: 4px solid #33b5a3;
}
.advantages__item--special .advantages__title {
  margin: 0 0 9px;
  padding-left: 0;
  text-align: center;
}
.advantages__item--special .advantages__feature {
  padding-left: 0;
  font-size: 64px;
  font-weight: 500;
  line-height: 60px;
  letter-spacing: -0.5px;
  text-align: center;
  color: #000000;
}
.designs {
  padding-top: 127px;
  overflow: hidden;
}
.designs__wrapper {
  width: 980px;
  margin: 0 auto;
}
.designs__title {
  margin: 0 0 6px;
  font-size: 40px;
  font-weight: 500;
  line-height: 50px;
  letter-spacing: -0.1px;
  color: #000000;
}
.designs__description {
  margin: 0 0 52px;
  padding-left: 3px;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.62px;
  color: #33b5a3;
}
.designs__container {
  width: 980px;
  margin: 0 auto;
}
.designs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.designs__item {
  flex-shrink: 0;
  margin: 0 165px 0 0;
}
.designs__item figure {
  display: flex;
  margin: 0;
}
.designs__item figure figcaption {
  display: none;
  width: 145px;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  color: #666666;
}
.designs__item--active {
  margin: 0 70px 0 0;
}
.designs__item--active figure picture {
  margin-right: 18px;
}
.designs__item--active figure figcaption {
  display: block;
}
.designs__item:last-child {
  margin: 0 0 0 0;
}
.slider__container {
  transition: transform 0.5s;
}
.slider__wrapper {
  position: relative;
}
.slider__controls {
  position: absolute;
  bottom: -6px;
  right: 65px;
  display: flex;
  justify-content: space-between;
  width: 83px;
}
.slider__btn {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.slider__btn svg {
  fill: none;
}
.slider__btn:disabled {
  opacity: 0.3;
}
